import React, { useState, useEffect } from 'react';
import { ArrowRight, Code2, Sparkles } from 'lucide-react';
import { Button } from './ui/button';
import { personalInfo } from '../data/mock';

const Hero = () => {
  const [displayedText, setDisplayedText] = useState('');
  const fullText = personalInfo.title;
  const [isTypingComplete, setIsTypingComplete] = useState(false);

  useEffect(() => {
    let index = 0;
    const typingInterval = setInterval(() => {
      if (index <= fullText.length) {
        setDisplayedText(fullText.slice(0, index));
        index++;
      } else {
        setIsTypingComplete(true);
        clearInterval(typingInterval);
      }
    }, 100);

    return () => clearInterval(typingInterval);
  }, [fullText]);

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <section
      id="hero"
      className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 text-white relative overflow-hidden"
    >
      {/* Enhanced Animated Background */}
      <div className="absolute inset-0 overflow-hidden">
        <div className="absolute w-96 h-96 bg-blue-500/10 rounded-full blur-3xl top-20 left-10 animate-pulse"></div>
        <div className="absolute w-96 h-96 bg-cyan-500/10 rounded-full blur-3xl bottom-20 right-10 animate-pulse" style={{ animationDelay: '1s' }}></div>
        <div className="absolute w-64 h-64 bg-blue-400/5 rounded-full blur-2xl top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 animate-pulse" style={{ animationDelay: '2s' }}></div>
        
        {/* Grid Pattern Overlay */}
        <div className="absolute inset-0 bg-grid-pattern opacity-5"></div>
      </div>

      {/* Content */}
      <div className="container mx-auto px-4 py-20 relative z-10">
        <div className="max-w-4xl mx-auto text-center">
          {/* Greeting Badge */}
          <div className="inline-flex items-center space-x-2 bg-gradient-to-r from-blue-500/20 to-cyan-500/20 border border-blue-500/30 rounded-full px-5 py-2 mb-8 animate-fade-in backdrop-blur-sm hover:scale-105 transition-transform duration-300">
            <Sparkles className="w-4 h-4 text-blue-400 animate-pulse" />
            <span className="text-sm text-blue-400 font-medium">Welcome to my portfolio</span>
          </div>

          {/* Main Heading with Stagger Animation */}
          <h1 className="text-5xl md:text-7xl font-bold mb-6 animate-slide-up">
            Hi, I'm{' '}
            <span className="relative inline-block">
              <span className="bg-gradient-to-r from-blue-400 via-cyan-400 to-blue-500 bg-clip-text text-transparent animate-gradient">
                {personalInfo.name}
              </span>
              <span className="absolute -inset-1 bg-gradient-to-r from-blue-400 to-cyan-400 rounded-lg blur-xl opacity-30 group-hover:opacity-50 transition-opacity"></span>
            </span>
          </h1>

          {/* Typing Animation Title */}
          <div className="h-20 md:h-24 mb-6">
            <h2 className="text-2xl md:text-4xl font-semibold text-gray-300 animate-slide-up delay-200">
              {displayedText}
              <span className={`inline-block w-1 h-8 md:h-10 bg-blue-400 ml-1 animate-blink ${
                isTypingComplete ? 'opacity-0' : 'opacity-100'
              }`}></span>
            </h2>
          </div>

          {/* Tagline */}
          <p className="text-lg md:text-xl text-gray-400 mb-10 max-w-2xl mx-auto animate-slide-up delay-300 leading-relaxed">
            {personalInfo.tagline}
          </p>

          {/* Enhanced CTA Buttons */}
          <div className="flex flex-col sm:flex-row items-center justify-center gap-4 animate-slide-up delay-400">
            <Button
              onClick={() => scrollToSection('projects')}
              size="lg"
              className="relative bg-gradient-to-r from-blue-500 to-cyan-500 hover:from-blue-600 hover:to-cyan-600 text-white px-8 py-6 text-lg group overflow-hidden border-0"
            >
              <span className="absolute inset-0 bg-white/20 transform scale-x-0 group-hover:scale-x-100 transition-transform duration-500 origin-left"></span>
              <span className="relative z-10 flex items-center">
                View My Work
                <ArrowRight className="ml-2 w-5 h-5 group-hover:translate-x-1 transition-transform" />
              </span>
            </Button>
            <Button
              onClick={() => scrollToSection('contact')}
              size="lg"
              variant="outline"
              className="relative border-2 border-blue-500 text-blue-400 hover:bg-blue-500 hover:text-white px-8 py-6 text-lg group overflow-hidden transition-all duration-300"
            >
              <span className="relative z-10">Get In Touch</span>
            </Button>
          </div>

          {/* Scroll Indicator */}
          <div className="mt-20 animate-bounce">
            <div className="w-6 h-10 border-2 border-blue-400 rounded-full mx-auto flex items-start justify-center p-2 hover:border-cyan-400 transition-colors cursor-pointer">
              <div className="w-1.5 h-3 bg-blue-400 rounded-full animate-scroll"></div>
            </div>
            <p className="text-sm text-gray-500 mt-3">Scroll to explore</p>
          </div>
        </div>
      </div>

      {/* Floating Elements */}
      <div className="absolute top-20 left-10 w-20 h-20 border border-blue-500/20 rounded-lg rotate-45 animate-float"></div>
      <div className="absolute bottom-32 right-20 w-16 h-16 border border-cyan-500/20 rounded-full animate-float" style={{ animationDelay: '1s' }}></div>
    </section>
  );
};

export default Hero;
